import React, { createContext, useState, useContext, useCallback, ReactNode, useRef } from 'react';
import { Task } from '../types';
import axios from 'axios';
import { normalizeMongoData, normalizeId } from '../utils/dataUtils';
import { taskAPI } from '../services/api';

// API URL
const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000/api';

interface TasksState {
  tasks: Task[];
  loading: boolean;
  error: string | null;
}

interface TaskContextProps {
  tasks: Task[];
  loading: boolean;
  error: string | null;
  loadTasks: (projectId: string) => Promise<Task[]>;
  addTask: (projectId: string, taskData: Partial<Task>) => Promise<Task | null>;
  updateTask: (taskId: string, taskData: Partial<Task>) => Promise<Task | null>;
  deleteTask: (taskId: string) => Promise<boolean>;
  moveTask: (taskId: string, newStatus: Task['status']) => Promise<Task | null>;
  clearTasks: () => void;
}

const TaskContext = createContext<TaskContextProps | undefined>(undefined);

// Grev istek nbellei iin tanmlama
interface TaskCache {
  [projectId: string]: {
    tasks: Task[];
    timestamp: number;
  };
}

export const TaskProvider: React.FC<{ children: ReactNode }> = ({ children }) => {
  const [state, setState] = useState<TasksState>({
    tasks: [],
    loading: false,
    error: null,
  });

  // nbellek iin bir referans olutur (re-render'lar arasnda korunsun)
  const taskCacheRef = useRef<TaskCache>({});
  // stek izleme iin referans (ayn anda ayn istek iin)
  const pendingRequestsRef = useRef<{[key: string]: boolean}>({});
  // En son istek zaman damgas
  const lastRequestTimesRef = useRef<{[key: string]: number}>({});

  // nbellek iin maksimum ya (10 dakika)
  const CACHE_MAX_AGE = 10 * 60 * 1000; // 5 dakikadan 10 dakikaya karyoruz
  
  // Tekrar istek yapma aral (5 saniye)
  const DEBOUNCE_TIME = 5000; // 3 saniyeden 5 saniyeye karyoruz

  // Projeye ait grevleri ykle
  const loadTasks = useCallback(async (projectId: string): Promise<Task[]> => {
    if (!projectId) {
      console.error('loadTasks: projectId eksik veya bo');
      return [];
    }
    
    try {
      // stek debouncing - ayn projeye belirli saniye iinde birden fazla istek gndermeyi engelle
      const now = Date.now();
      const lastRequestTime = lastRequestTimesRef.current[projectId] || 0;
      if (now - lastRequestTime < DEBOUNCE_TIME) {
        console.log(`Son ${DEBOUNCE_TIME/1000} saniye iinde ${projectId} iin tasks istei yapld, tekrar istek yaplmyor`);
        
        // Eer nbellekte veri varsa onu dndr
        if (taskCacheRef.current[projectId] && taskCacheRef.current[projectId].tasks.length > 0) {
          return taskCacheRef.current[projectId].tasks;
        }
        
        // Eer hala istek devam ediyorsa bekle
        if (pendingRequestsRef.current[projectId]) {
          console.log(`${projectId} iin hala devam eden istek var, bekleyin...`);
          setState(prev => ({ ...prev, loading: true }));
          return state.tasks; // Mevcut grevleri dndr
        }
      }
      
      // Son istek zamann gncelle
      lastRequestTimesRef.current[projectId] = now;
      
      // nbellek kontrol
      const cachedData = taskCacheRef.current[projectId];
      if (cachedData && now - cachedData.timestamp < CACHE_MAX_AGE) {
        console.log(`nbellekten ${projectId} iin grevler ykleniyor`);
        return cachedData.tasks;
      }
      
      // Eer ayn proje iin istek zaten devam ediyorsa
      if (pendingRequestsRef.current[projectId]) {
        console.log(`${projectId} iin istek zaten devam ediyor`);
        return state.tasks; // Mevcut grevleri dndr
      }
      
      setState(prev => ({ ...prev, loading: true, error: null }));
      
      // stei balat
      pendingRequestsRef.current[projectId] = true;
      
      // Token dorudan alnyor
      const token = localStorage.getItem('token');
      if (!token) {
        console.error('Token bulunamad, grevler yklenemiyor');
        setState(prev => ({
          ...prev,
          error: 'Oturum bilgileriniz eksik veya sresi dolmu',
          loading: false,
        }));
        pendingRequestsRef.current[projectId] = false;
        return [];
      }
      
      // Token format kontrol (basit)
      if (!token.match(/^[A-Za-z0-9-_]+\.[A-Za-z0-9-_]+\.[A-Za-z0-9-_.+/=]*$/)) {
        console.error('Token format geersiz:', token.substring(0, 10) + '...');
        setState(prev => ({
          ...prev,
          error: 'Oturum bilginiz geersiz, ltfen tekrar giri yapn',
          loading: false,
        }));
        pendingRequestsRef.current[projectId] = false;
        return [];
      }
      
      console.log(`Grevler ykleniyor, proje ID: ${projectId}`);
      
      // AbortController ekle
      const controller = new AbortController();
      const timeoutId = setTimeout(() => {
        console.log('Grev ykleme ilemi zaman amna urad, iptal ediliyor...');
        controller.abort();
      }, 12000);
      
      try {
        // Token aka belirtiliyor
        const response = await axios.get(`${API_URL}/tasks/project/${projectId}`, {
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json',
            'Cache-Control': 'no-cache, no-store, must-revalidate',
            'Pragma': 'no-cache',
            'Expires': '0'
          },
          timeout: 12000,
          signal: controller.signal
        });
        
        // Zamanlaycy temizle
        clearTimeout(timeoutId);
        
        const loadedTasks = response.data;
        console.log('Yklenen grevler:', loadedTasks);
        
        // MongoDB'den gelen verileri normalize et
        const normalizedTasks = normalizeMongoData<Task[]>(loadedTasks);
        console.log('Normalize edilmi grevler:', normalizedTasks);
        
        // Grevleri nbellee al
        taskCacheRef.current[projectId] = {
          tasks: normalizedTasks,
          timestamp: Date.now()
        };
        
        setState(prev => ({
          ...prev,
          tasks: normalizedTasks,
          loading: false,
        }));
        
        // stei kapat
        pendingRequestsRef.current[projectId] = false;
        
        return normalizedTasks;
      } catch (requestError: any) {
        // Zamanlaycy temizle
        clearTimeout(timeoutId);
        
        console.error('Grevler yklenirken istek hatas:', requestError);
        let errorMessage = 'Grevler yklenemedi';
        
        if (requestError.name === 'AbortError' || requestError.code === 'ECONNABORTED') {
          errorMessage = 'Sunucu yant vermiyor, ltfen daha sonra tekrar deneyin';
        } else if (requestError.response) {
          // HTTP hata yant var
          const status = requestError.response.status;
          
          if (status === 401 || status === 403) {
            errorMessage = 'Oturum bilgileriniz geersiz veya yetkisiz, ltfen tekrar giri yapn';
            // Token sorunlu olabilir, dier ilemlerde de hata almamak iin localStorage'dan temizleyelim
            if (status === 401) {
              console.warn('Yetkilendirme baarsz, tokeni temizliyorum');
              localStorage.removeItem('token');
            }
          } else if (status === 404) {
            errorMessage = 'Proje bulunamad veya silinmi olabilir';
          } else if (requestError.response.data?.error) {
            errorMessage = requestError.response.data.error;
          } else {
            errorMessage = `Sunucu hatas: ${status}`;
          }
        } else if (requestError.message) {
          errorMessage = requestError.message;
        }
        
        setState(prev => ({
          ...prev,
          error: errorMessage,
          loading: false,
        }));
        
        // stei kapat
        pendingRequestsRef.current[projectId] = false;
        
        return [];
      }
    } catch (error: any) {
      console.error('Grevler yklenirken genel hata:', error);
      let errorMessage = 'Grevler yklenemedi';
      
      if (error.response?.data?.error) {
        errorMessage = error.response.data.error;
      } else if (error.code === 'ECONNABORTED') {
        errorMessage = 'Sunucu yant vermiyor, ltfen daha sonra tekrar deneyin';
      } else if (error.message) {
        errorMessage = error.message;
      }
      
      setState(prev => ({
        ...prev,
        error: errorMessage,
        loading: false,
      }));
      
      // stei kapat
      pendingRequestsRef.current[projectId] = false;
      
      return [];
    }
  }, [state.tasks]);

  // Yeni grev eklendiinde nbellei temizleyen yardmc fonksiyon
  const invalidateTaskCache = useCallback((projectId: string) => {
    if (projectId && taskCacheRef.current[projectId]) {
      console.log(`${projectId} iin grev nbellei temizleniyor`);
      delete taskCacheRef.current[projectId];
    }
  }, []);

  // Yeni grev olutur
  const addTask = useCallback(async (projectId: string, taskData: Partial<Task>): Promise<Task | null> => {
    if (!projectId) {
      console.error('addTask: projectId eksik veya bo');
      return null;
    }
    
    try {
      setState(prev => ({ ...prev, loading: true, error: null }));
      
      // Token dorudan alnyor
      const token = localStorage.getItem('token');
      if (!token) {
        console.error('Token bulunamad, grev eklenemiyor');
        setState(prev => ({
          ...prev,
          error: 'Oturum bilgileriniz eksik veya sresi dolmu',
          loading: false,
        }));
        return null;
      }
      
      console.log(`Grev ekleniyor, proje ID: ${projectId}, grev verileri:`, taskData);
      
      // Default status deeri ayarlayalm
      const taskWithDefaults = {
        ...taskData,
        status: taskData.status || 'yapiliyor'
      };
      
      // Hzl yant iin AbortController ekle
      const controller = new AbortController();
      const timeoutId = setTimeout(() => {
        console.log('Grev ekleme ilemi zaman amna urad, iptal ediliyor...');
        controller.abort();
      }, 15000); // 15 saniye sonra iptal et
      
      // stek yaplandrmas
      const config = {
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json',
          'Cache-Control': 'no-cache, no-store, must-revalidate',
          'Pragma': 'no-cache',
          'Expires': '0'
        },
        timeout: 15000,
        signal: controller.signal
      };
      
      try {
        // Token aka belirtiliyor
        const response = await axios.post(`${API_URL}/tasks/project/${projectId}`, taskWithDefaults, config);
        
        // AbortController iin zamanlaycy temizle
        clearTimeout(timeoutId);
        
        const newTask = response.data;
        console.log('Yeni grev oluturuldu (ham veri):', newTask);
        
        // MongoDB'den gelen veriyi normalize et
        const normalizedTask = normalizeMongoData<Task>(newTask);
        console.log('Yeni grev normalize edildi:', normalizedTask);
        
        // Grev eklendiinde proje nbelleini temizle
        invalidateTaskCache(projectId);
        
        setState(prev => ({
          ...prev,
          tasks: [...prev.tasks, normalizedTask],
          loading: false,
        }));
        
        return normalizedTask;
      } catch (requestError: any) {
        // AbortController iin zamanlaycy temizle
        clearTimeout(timeoutId);
        
        console.error('Grev oluturma istei hatas:', requestError);
        let errorMessage = 'Grev oluturulamad';
        
        if (requestError.name === 'AbortError' || requestError.code === 'ECONNABORTED') {
          errorMessage = 'Sunucu yant vermiyor, ltfen daha sonra tekrar deneyin';
        } else if (requestError.response?.data?.error) {
          errorMessage = requestError.response.data.error;
          console.error('Sunucu hata mesaj:', errorMessage);
        } else if (requestError.message) {
          errorMessage = requestError.message;
        }
        
        setState(prev => ({
          ...prev,
          error: errorMessage,
          loading: false,
        }));
        
        return null;
      }
    } catch (error: any) {
      console.error('Grev oluturma genel hatas:', error);
      let errorMessage = 'Grev oluturulamad: Beklenmeyen bir hata olutu';
      
      if (error.response?.data?.error) {
        errorMessage = error.response.data.error;
        console.error('Sunucu hata mesaj:', errorMessage);
      } else if (error.code === 'ECONNABORTED') {
        errorMessage = 'Sunucu yant vermiyor, ltfen daha sonra tekrar deneyin';
      } else if (error.message) {
        errorMessage = error.message;
      }
      
      setState(prev => ({
        ...prev,
        error: errorMessage,
        loading: false,
      }));
      
      return null;
    }
  }, [invalidateTaskCache]);

  // Grevi gncelle
  const updateTask = useCallback(async (taskId: string, taskData: Partial<Task>): Promise<Task | null> => {
    if (!taskId) {
      console.error('updateTask: taskId eksik veya bo');
      return null;
    }
    
    try {
      setState(prev => ({ ...prev, loading: true, error: null }));
      
      // Token dorudan alnyor
      const token = localStorage.getItem('token');
      if (!token) {
        console.error('Token bulunamad, grev gncellenemiyor');
        setState(prev => ({
          ...prev,
          error: 'Oturum bilgileriniz eksik veya sresi dolmu',
          loading: false,
        }));
        return null;
      }
      
      console.log(`Grev gncelleniyor, ID: ${taskId}, gncellenen veriler:`, taskData);
      
      // Hzl yant iin AbortController ekle
      const controller = new AbortController();
      const timeoutId = setTimeout(() => {
        console.log('Grev gncelleme ilemi zaman amna urad, iptal ediliyor...');
        controller.abort();
      }, 10000); // 10 saniye sonra iptal et
      
      try {
        // Token aka belirtiliyor
        const response = await axios.put(`${API_URL}/tasks/${taskId}`, taskData, {
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json',
            'Cache-Control': 'no-cache, no-store, must-revalidate',
            'Pragma': 'no-cache',
            'Expires': '0'
          },
          timeout: 10000,
          signal: controller.signal
        });
        
        // AbortController iin zamanlaycy temizle
        clearTimeout(timeoutId);
        
        const updatedTask = response.data;
        console.log('Grev gncellendi (ham veri):', updatedTask);
        
        // MongoDB'den gelen veriyi normalize et
        const normalizedTask = normalizeMongoData<Task>(updatedTask);
        console.log('Grev normalize edildi:', normalizedTask);
        
        // nbellei gncelle (ilgili projeyi bul)
        const affectedProjectId = normalizedTask.project as string;
        if (affectedProjectId) {
          invalidateTaskCache(affectedProjectId);
        }
        
        // Gncellenen grevi state'te de gncelle - hem id hem de _id ile eletir
        setState(prev => ({
          ...prev,
          tasks: prev.tasks.map(task => 
            normalizeId(task) === normalizeId(taskId) ? { ...task, ...normalizedTask } : task
          ),
          loading: false,
        }));
        
        return normalizedTask;
      } catch (requestError: any) {
        // AbortController iin zamanlaycy temizle
        clearTimeout(timeoutId);
        
        console.error('Grev gncelleme istei hatas:', requestError);
        let errorMessage = 'Grev gncellenemedi';
        
        if (requestError.name === 'AbortError' || requestError.code === 'ECONNABORTED') {
          errorMessage = 'Sunucu yant vermiyor, ltfen daha sonra tekrar deneyin';
        } else if (requestError.response?.data?.error) {
          errorMessage = requestError.response.data.error;
          console.error('Sunucu hata mesaj:', errorMessage);
        } else if (requestError.message) {
          errorMessage = requestError.message;
        }
        
        setState(prev => ({
          ...prev,
          error: errorMessage,
          loading: false,
        }));
        
        return null;
      }
    } catch (error: any) {
      console.error('Grev gncelleme genel hatas:', error);
      let errorMessage = 'Grev gncellenemedi: Beklenmeyen bir hata olutu';
      
      if (error.response?.data?.error) {
        errorMessage = error.response.data.error;
      } else if (error.code === 'ECONNABORTED') {
        errorMessage = 'Sunucu yant vermiyor, ltfen daha sonra tekrar deneyin';
      } else if (error.message) {
        errorMessage = error.message;
      }
      
      setState(prev => ({
        ...prev,
        error: errorMessage,
        loading: false,
      }));
      
      return null;
    }
  }, [invalidateTaskCache]);

  // Grevi sil
  const deleteTask = useCallback(async (taskId: string): Promise<boolean> => {
    if (!taskId) {
      console.error('TaskContext - deleteTask: taskId eksik veya bo');
      return false;
    }
    
    try {
      console.log('TaskContext - Grev silme ilemi balatlyor, ID:', taskId);
      setState(prev => ({ ...prev, loading: true, error: null }));
      
      // Grev silme ilemi ncesinde, etkilenecek proje ID'sini bulmak iin
      // ilgili grevi bul (nbellek gncellemesi iin kullanlacak)
      const taskToDelete = state.tasks.find(task => 
        normalizeId(task) === normalizeId(taskId)
      );
      
      const affectedProjectId = taskToDelete?.project as string;
      console.log('TaskContext - Etkilenecek proje ID:', affectedProjectId);
      
      try {
        // taskAPI zerinden silme ilemini gerekletir
        console.log('TaskContext - taskAPI.deleteTask arlyor...');
        const success = await taskAPI.deleteTask(taskId);
        console.log('TaskContext - API silme ilemi sonucu:', success);
        
        if (success) {
          // Silinen grevi listeden kaldr
          setState(prev => {
            console.log('TaskContext - State gncelleniyor, silinen grev ID:', taskId);
            
            // Hem id hem de _id ile karlatrarak filtrele
            const updatedTasks = prev.tasks.filter(task => 
              normalizeId(task) !== normalizeId(taskId)
            );
            
            console.log('TaskContext - Filtreleme sonras kalan grev says:', updatedTasks.length);
            
            return {
              ...prev,
              tasks: updatedTasks,
              loading: false,
              error: null
            };
          });
          
          // Etkilenen proje varsa nbellei temizle
          if (affectedProjectId) {
            console.log('TaskContext - Etkilenen proje nbellei temizleniyor:', affectedProjectId);
            invalidateTaskCache(affectedProjectId);
          }
          
          return true;
        } else {
          setState(prev => ({
            ...prev,
            error: 'Grev silme ilemi baarsz oldu',
            loading: false,
          }));
          return false;
        }
      } catch (apiError: any) {
        console.error('TaskContext - API silme ilemi hatas:', apiError);
        
        let errorMessage = 'Grev silinemedi';
        if (apiError.response?.data?.error) {
          errorMessage = apiError.response.data.error;
        } else if (apiError.message) {
          errorMessage = apiError.message;
        }
        
        setState(prev => ({
          ...prev,
          error: errorMessage,
          loading: false,
        }));
        
        throw apiError;
      }
    } catch (error: any) {
      console.error('TaskContext - Grev silme genel hatas:', error);
      
      let errorMessage = 'Grev silinemedi';
      if (error.response?.data?.error) {
        errorMessage = error.response.data.error;
      } else if (error.message) {
        errorMessage = error.message;
      }
      
      setState(prev => ({
        ...prev,
        error: errorMessage,
        loading: false,
      }));
      
      return false;
    }
  }, [state.tasks, invalidateTaskCache]);

  // Grevi ta (durumunu deitir)
  const moveTask = useCallback(async (taskId: string, newStatus: Task['status']): Promise<Task | null> => {
    console.log(`Grev tanyor: ${taskId} -> ${newStatus}`);
    return updateTask(taskId, { status: newStatus });
  }, [updateTask]);

  // Grevleri temizle
  const clearTasks = useCallback(() => {
    console.log('Grevler temizleniyor');
    setState(prev => ({
      ...prev,
      tasks: [],
      error: null,
    }));
  }, []);

  const value = {
    tasks: state.tasks,
    loading: state.loading,
    error: state.error,
    loadTasks,
    addTask,
    updateTask,
    deleteTask,
    moveTask,
    clearTasks,
  };

  return <TaskContext.Provider value={value}>{children}</TaskContext.Provider>;
};

export const useTaskContext = () => {
  const context = useContext(TaskContext);
  if (context === undefined) {
    throw new Error('useTaskContext must be used within a TaskProvider');
  }
  return context;
};

export default TaskContext; 