/**
 * MongoDB ile frontend arasndaki ID dnmlerini kolaylatran yardmc fonksiyonlar
 */

/**
 * MongoDB verisini frontend iin normalize eder.
 * _id alann id alanna kopyalar ve tutarl bir veri yaps salar.
 */
export const normalizeMongoData = <T extends object>(data: any): T => {
  if (!data) return data;
  
  // Tek bir belge iin normalizasyon
  if (typeof data === 'object' && !Array.isArray(data)) {
    const normalized = { ...data };
    
    // MongoDB'nin _id alann frontend'in id alanna kopyala
    if (data._id && !data.id) {
      normalized.id = data._id;
    }
    
    return normalized as T;
  }
  
  // Belge dizisi iin normalizasyon 
  if (Array.isArray(data)) {
    return data.map(item => normalizeMongoData(item)) as unknown as T;
  }
  
  return data;
};

/**
 * Bir ID deerini normalize eder (MongoDB _id veya frontend id)
 */
export const normalizeId = (item: any): string => {
  if (!item) return '';
  
  if (typeof item === 'string') return item;
  
  return (item.id || item._id || '').toString();
}; 