import axios from 'axios';
import { Task, Project } from '../types';

// API temel URL
const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000/api';

// JWT token'n localStorage'dan alma
const getToken = () => {
  try {
    const token = localStorage.getItem('token');
    
    // Token varsa format kontrol yap
    if (token) {
      // JWT format: xxxxx.yyyyy.zzzzz
      const parts = token.split('.');
      if (parts.length !== 3) {
        console.error('Geersiz token format, token temizleniyor');
        localStorage.removeItem('token');
        return null;
      }
      
      return token;
    }
    
    return null;
  } catch (error) {
    console.error('Token alnrken hata olutu:', error);
    return null;
  }
};

// Axios instance oluturma
const api = axios.create({
  baseURL: API_URL,
  headers: {
    'Content-Type': 'application/json',
  },
  timeout: 8000, // 10 saniyeden 8 saniyeye drlyor
});

// stek iin Authorization header'n ayarlama
api.interceptors.request.use(
  (config) => {
    try {
      const token = getToken();
      
      // stisna olmad ve ayarlanm config.headers olduu srece devam et
      if (config.headers) {
        if (token) {
          // Token' ekleyelim
          config.headers['Authorization'] = `Bearer ${token}`;
          
          // Baz zel endpoint'lerde log yazmayalm (salk kontrol gibi)
          if (!config.url?.includes('/health')) {
            console.log(`API istei gnderiliyor (${config.method?.toUpperCase()}): ${config.url}`);
          }
        } else {
          // Token bulunamadysa Authorization headern temizle
          delete config.headers['Authorization'];
          
          // Sadece yetkili endpoint'lerde uyar ver
          if (!config.url?.includes('/auth/login') && 
              !config.url?.includes('/auth/register') && 
              !config.url?.includes('/health')) {
            console.warn(`API istei iin token bulunamad (${config.method?.toUpperCase()}): ${config.url}`);
          }
        }
      }
      
      return config;
    } catch (error) {
      console.error('API istei yaplrken hata:', error);
      return config;
    }
  },
  (error) => Promise.reject(error)
);

// Response interceptor - Herhangi bir hata durumunda kullanc dostu hata gsterimi
api.interceptors.response.use(
  response => {
    // Salk kontrol iin loglar gsterme
    if (!response.config.url?.includes('/health')) {
      // Baarl yantlar sadece nemli durumlarda logla
      if (response.config.method !== 'get') {
        console.log(`API yant baarl (${response.status}): ${response.config.method?.toUpperCase()} ${response.config.url}`);
      }
    }
    return response;
  },
  error => {
    // Aborted hatas iin zel ileme
    if (error.name === 'AbortError' || error.code === 'ECONNABORTED') {
      console.warn('API istei zaman am ile iptal edildi:', error.config?.url);
      error.friendlyMessage = 'stek zaman amna urad, ltfen tekrar deneyin.';
      return Promise.reject(error);
    }
    
    // Herhangi bir yant yoksa veya network hatas varsa
    if (!error.response) {
      console.error('API yant alnamad:', error.message);
      // stemcide hatay gstermek iin mesaj iyiletir
      error.friendlyMessage = 'Sunucuya balanlamyor. Ltfen internet balantnz kontrol edin.';
      return Promise.reject(error);
    }
    
    // Salk kontrol iin loglar gsterme
    if (!error.config?.url?.includes('/health')) {
      console.error(`API hatas (${error.response.status}): ${error.config?.method?.toUpperCase()} ${error.config?.url}`, 
        error.response?.data?.error || error.message);
    }
    
    // 401 (Unauthorized) hatas durumunda
    if (error.response.status === 401) {
      // Salk kontrol iin yetki hatalarn yok say
      if (!error.config?.url?.includes('/health')) {
        console.warn('Yetkisiz eriim hatas tespit edildi:', error.response.data?.error);
        
        // Oturum hatas mesajn kaydet
        error.authError = true;
        error.friendlyMessage = 'Oturum sresi dolmu olabilir. Ltfen tekrar giri yapn.';
      }
    }
    
    return Promise.reject(error);
  }
);

// Project API fonksiyonlar
export const projectAPI = {
  // Tm projeleri getirme
  getProjects: async (): Promise<Project[]> => {
    try {
      const response = await api.get('/projects');
      
      // MongoDB _id ile client id uyumunu sala
      const projects = response.data.map((project: any) => ({
        ...project,
        id: project.id || project._id
      }));
      
      return projects;
    } catch (error) {
      console.error('Projeler alnrken hata olutu:', error);
      return []; // Hata durumunda bo array dndr
    }
  },

  // Tekil proje detay getirme
  getProjectById: async (projectId: string): Promise<Project> => {
    try {
      // projectId kontrol
      if (!projectId || projectId === 'undefined') {
        console.error('Geersiz proje ID:', projectId);
        throw new Error('Geersiz proje ID');
      }
      
      const response = await api.get(`/projects/${projectId}`);
      
      // MongoDB _id ile client id uyumunu sala
      const project = response.data;
      return {
        ...project,
        id: project.id || project._id
      };
    } catch (error) {
      console.error('Proje detay alnrken hata olutu:', error);
      throw error;
    }
  },

  // Yeni proje oluturma
  createProject: async (projectData: Partial<Project>): Promise<Project> => {
    try {
      const response = await api.post('/projects', projectData);
      
      // MongoDB _id ile client id uyumunu sala
      const project = response.data;
      return {
        ...project,
        id: project.id || project._id
      };
    } catch (error) {
      console.error('Proje oluturulurken hata olutu:', error);
      throw error;
    }
  },

  // Proje gncelleme
  updateProject: async (projectId: string, projectData: Partial<Project>): Promise<Project> => {
    try {
      // projectId kontrol
      if (!projectId || projectId === 'undefined') {
        console.error('Geersiz proje ID:', projectId);
        throw new Error('Geersiz proje ID');
      }
      
      const response = await api.put(`/projects/${projectId}`, projectData);
      
      // MongoDB _id ile client id uyumunu sala
      const project = response.data;
      return {
        ...project,
        id: project.id || project._id
      };
    } catch (error) {
      console.error('Proje gncellenirken hata olutu:', error);
      throw error;
    }
  },

  // Proje silme
  deleteProject: async (projectId: string): Promise<boolean> => {
    try {
      // projectId kontrol
      if (!projectId || projectId === 'undefined') {
        console.error('Geersiz proje ID:', projectId);
        throw new Error('Geersiz proje ID');
      }
      
      console.log('API silme ilemi balatlyor, Proje ID:', projectId);
      
      // Hzl yant iin zel ayarlar
      const controller = new AbortController();
      const timeoutId = setTimeout(() => controller.abort(), 5000); // 5 saniye sonra iptal et
      
      const response = await api.delete(`/projects/${projectId}`, {
        signal: controller.signal,
        timeout: 5000 // Daha dk timeout
      });
      
      clearTimeout(timeoutId);
      console.log('API silme cevab:', response.data);
      
      return true;
    } catch (error: any) {
      console.error('Proje silinirken hata olutu:', error);
      
      // Hata detaylarn kontrol et
      if (error.response) {
        console.error('Hata yant:', error.response.status, error.response.data);
      } else if (error.name === 'AbortError') {
        console.error('stek zaman am nedeniyle iptal edildi');
      }
      
      throw error;
    }
  },
};

// Task API fonksiyonlar
export const taskAPI = {
  // Projeye ait tm grevleri getirme
  getTasks: async (projectId: string): Promise<Task[]> => {
    try {
      // projectId kontrol
      if (!projectId || projectId === 'undefined') {
        console.error('Geersiz proje ID:', projectId);
        return []; // Hata durumunda bo array dndr
      }
      
      const response = await api.get(`/tasks/project/${projectId}`);
      
      // MongoDB _id ile client id uyumunu sala
      return response.data.map((task: any) => ({
        ...task,
        id: task.id || task._id
      }));
    } catch (error) {
      console.error('Grevler alnrken hata olutu:', error);
      return []; // Hata durumunda bo array dndr
    }
  },

  // Tekil grev detay getirme
  getTaskById: async (taskId: string): Promise<Task> => {
    try {
      // taskId kontrol
      if (!taskId || taskId === 'undefined') {
        console.error('Geersiz grev ID:', taskId);
        throw new Error('Geersiz grev ID');
      }
      
      const response = await api.get(`/tasks/${taskId}`);
      
      // MongoDB _id ile client id uyumunu sala
      const task = response.data;
      return {
        ...task,
        id: task.id || task._id
      };
    } catch (error) {
      console.error('Grev detay alnrken hata olutu:', error);
      throw error;
    }
  },

  // Yeni grev oluturma
  createTask: async (projectId: string, taskData: Partial<Task>): Promise<Task> => {
    try {
      // projectId kontrol
      if (!projectId || projectId === 'undefined') {
        console.error('Geersiz proje ID:', projectId);
        throw new Error('Geersiz proje ID');
      }
      
      const url = `/tasks/project/${projectId}`;
      
      // Default status deeri ayarlayalm
      const taskWithDefaults = {
        ...taskData,
        status: taskData.status || 'yapiliyor'
      };
      
      const response = await api.post(url, taskWithDefaults);
      
      // MongoDB _id ile client id uyumunu sala
      const task = response.data;
      return {
        ...task,
        id: task.id || task._id
      };
    } catch (error: any) {
      console.error('Grev oluturulurken hata olutu:', error);
      
      if (error.response?.data?.error) {
        console.error('Sunucu hata mesaj:', error.response.data.error);
      }
      
      throw error;
    }
  },

  // Grev gncelleme
  updateTask: async (taskId: string, taskData: Partial<Task>): Promise<Task> => {
    try {
      // taskId kontrol
      if (!taskId || taskId === 'undefined') {
        console.error('Geersiz grev ID:', taskId);
        throw new Error('Geersiz grev ID');
      }
      
      const response = await api.put(`/tasks/${taskId}`, taskData);
      
      // MongoDB _id ile client id uyumunu sala
      const task = response.data;
      return {
        ...task,
        id: task.id || task._id
      };
    } catch (error) {
      console.error('Grev gncellenirken hata olutu:', error);
      throw error;
    }
  },

  // Grev silme
  deleteTask: async (taskId: string): Promise<boolean> => {
    try {
      // taskId kontrol
      if (!taskId || taskId === 'undefined') {
        console.error('API - Geersiz grev ID:', taskId);
        throw new Error('Geersiz grev ID');
      }
      
      console.log('API - Silme ilemi balatlyor, Grev ID:', taskId);
      
      // Hzl yant iin zel ayarlar
      const controller = new AbortController();
      const timeoutId = setTimeout(() => {
        console.log('API - Silme ilemi zaman amna urad, iptal ediliyor...');
        controller.abort();
      }, 5000); // 5 saniye sonra iptal et
      
      try {
        console.log('API - DELETE istei gnderiliyor:', `/tasks/${taskId}`);
        
        const response = await api.delete(`/tasks/${taskId}`, {
          signal: controller.signal,
          timeout: 5000 // Daha dk timeout
        });
        
        clearTimeout(timeoutId);
        console.log('API - Grev silme cevab:', response.status, response.data);
        
        return true;
      } catch (requestError: any) {
        clearTimeout(timeoutId);
        
        // Eer istek zaman amna uradysa veya iptal edildiyse
        if (requestError.name === 'AbortError' || requestError.code === 'ECONNABORTED') {
          console.error('API - Silme istei zaman amna urad veya iptal edildi');
          throw new Error('stek zaman amna urad');
        }
        
        // Server yant verdiyse ve hata dndyse
        if (requestError.response) {
          console.error('API - Server hata yant:', requestError.response.status, requestError.response.data);
          
          // 401 veya 403 durumlar iin zel mesaj
          if (requestError.response.status === 401) {
            throw new Error('Oturum sresi dolmu');
          } else if (requestError.response.status === 403) {
            throw new Error('Bu ilem iin yetkiniz yok');
          } else if (requestError.response.status === 404) {
            throw new Error('Grev bulunamad');
          } else {
            throw new Error(requestError.response.data?.error || 'Sunucu hatas');
          }
        }
        
        // Dier hatalar
        console.error('API - Silme istei dier hata:', requestError.message);
        throw requestError;
      }
    } catch (error: any) {
      console.error('API - Grev silme genel hatas:', error.message);
      throw error;
    }
  },
};

// Authentication API fonksiyonlar
export const authAPI = {
  // Kullanc girii
  login: async (username: string, password: string) => {
    try {
      const response = await api.post('/auth/login', { username, password });
      return response.data;
    } catch (error) {
      console.error('Giri yaplrken hata olutu:', error);
      throw error;
    }
  },

  // Kullanc kayd
  register: async (userData: any) => {
    try {
      const response = await api.post('/auth/register', userData);
      return response.data;
    } catch (error) {
      console.error('Kayt olunurken hata olutu:', error);
      throw error;
    }
  },

  // Kullanc bilgilerini getirme
  getUser: async () => {
    try {
      const response = await api.get('/auth/me');
      return response.data;
    } catch (error) {
      console.error('Kullanc bilgileri alnrken hata olutu:', error);
      throw error;
    }
  },
};

export default api; 