import React, { createContext, useState, useContext, useEffect, useCallback, ReactNode } from 'react';
import axios from 'axios';
import { CircularProgress, Box } from '@mui/material';

// API URL
const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000/api';

// Salk kontrol durumunu ve zaman damgasn takip et
let lastConnectionCheck = 0;
let isConnectionCheckInProgress = false;
const connectionCheckCache = { isAvailable: true, timestamp: 0 };
const HEALTH_CHECK_INTERVAL = 300000; // 5 dakika (ms)

// API balantsn kontrol et (debounce mekanizmas ile)
const isServerAvailable = async (): Promise<boolean> => {
  try {
    const now = Date.now();
    
    // Son kontrolden bu yana 5 dakika gemediyse nbellei kullan
    if (now - connectionCheckCache.timestamp < HEALTH_CHECK_INTERVAL) {
      return connectionCheckCache.isAvailable;
    }
    
    // Eer kontrol zaten yaplyorsa yeni istek gnderme
    if (isConnectionCheckInProgress) {
      return connectionCheckCache.isAvailable; // Son bilinen deeri kullan
    }
    
    isConnectionCheckInProgress = true;
    console.log('API balants kontrol ediliyor...');
    
    try {
      // lk nce auth salk kontroln deneyelim
      const authResponse = await axios.get(`${API_URL}/auth/health`, { 
        timeout: 3000,
        validateStatus: (status) => true,
        headers: {
          'Cache-Control': 'no-cache, no-store, must-revalidate',
          'Pragma': 'no-cache',
          'Expires': '0'
        }
      });
      
      lastConnectionCheck = now;
      connectionCheckCache.timestamp = now;
      isConnectionCheckInProgress = false;
      
      if (authResponse.status === 200 || authResponse.status === 304) {
        console.log('Auth servis salk kontrol baarl:', authResponse.status);
        connectionCheckCache.isAvailable = true;
        return true;
      }
      
      console.log('Auth servis salk kontrol yant kodu:', authResponse.status);
      
      if (authResponse.status < 500) {
        // 4xx hatalar durumunda yine de sunucu alyor demektir
        connectionCheckCache.isAvailable = true;
        return true;
      }
      
      // imdi de tasks API'si deneyelim
      try {
        const tasksResponse = await axios.get(`${API_URL}/tasks/health`, { 
          timeout: 3000,
          validateStatus: (status) => true,
          headers: {
            'Cache-Control': 'no-cache, no-store, must-revalidate',
            'Pragma': 'no-cache',
            'Expires': '0'
          }
        });
        
        if (tasksResponse.status === 200 || tasksResponse.status === 304) {
          console.log('Tasks servis salk kontrol baarl:', tasksResponse.status);
          connectionCheckCache.isAvailable = true;
          return true;
        }
        
        if (tasksResponse.status < 500) {
          // 4xx hatalar durumunda yine de sunucu alyor demektir
          connectionCheckCache.isAvailable = true;
          return true;
        }
      } catch (tasksError: any) {
        console.warn('Tasks servis salk kontrolnde hata:', tasksError.message);
      }
      
      // Tm kontroller baarsz olduysa balant yok demektir
      connectionCheckCache.isAvailable = false;
      return false;
    } catch (error) {
      console.error('API balant kontrol hatas:', error);
      isConnectionCheckInProgress = false;
      connectionCheckCache.isAvailable = false;
      return false;
    }
  } catch (err) {
    console.error('API balant kontrol genel hatas:', err);
    isConnectionCheckInProgress = false;
    connectionCheckCache.isAvailable = false;
    return false;
  }
};

// Axios instance
const api = axios.create({
  baseURL: API_URL,
  headers: {
    'Content-Type': 'application/json'
  },
  timeout: 8000 // 10 saniyeden 8 saniyeye drlyor
});

// Token ile yaplacak istekler iin interceptor
api.interceptors.request.use(
  config => {
    const token = localStorage.getItem('token');
    if (token) {
      config.headers['Authorization'] = `Bearer ${token}`;
    }
    return config;
  },
  error => Promise.reject(error)
);

// Hata durumunda otomatik k iin response interceptor
api.interceptors.response.use(
  response => response,
  error => {
    // 401 (Unauthorized) hatas durumunda otomatik k
    if (error.response && error.response.status === 401) {
      console.warn('Yetkisiz eriim hatas tespit edildi, ancak otomatik k yaplmayacak');
      console.log('Oturum durumu kullanc tarafndan kontrol edilecek');
      
      // Token ve kullanc bilgisini silmeyelim, bunun yerine
      // checkTokenValidity fonksiyonunun kullanc tarafndan arldnda
      // doru sonu vermesini salayalm. Bu sayede sayfa yenilemesi veya
      // ynlendirme olmadan devam edilebilir.
    }
    return Promise.reject(error);
  }
);

// Kullanc ve context tipleri
interface User {
  id: string;
  name: string;
  surname: string;
  username: string;
  email: string;
}

interface AuthContextType {
  user: User | null;
  token: string | null;
  loading: boolean;
  error: string | null;
  login: (username: string, password: string) => Promise<boolean>;
  register: (userData: RegisterData) => Promise<boolean>;
  logout: () => void;
  setUser: (user: User | null) => void;
  clearError: () => void;
  checkTokenValidity: () => Promise<boolean>;
  isServerReachable: boolean;
}

interface RegisterData {
  name: string;
  surname: string;
  username: string;
  email: string;
  password: string;
  confirmPassword: string;
}

interface AuthProviderProps {
  children: ReactNode;
}

// Context olutur
const AuthContext = createContext<AuthContextType | null>(null);

// Provider component
export const AuthProvider: React.FC<AuthProviderProps> = ({ children }) => {
  const [user, setUser] = useState<User | null>(null);
  const [token, setToken] = useState<string | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [initialized, setInitialized] = useState(false);
  const [isServerReachable, setIsServerReachable] = useState(true); // Balangta balant var kabul edilir
  const [isCheckingConnection, setIsCheckingConnection] = useState(false);
  const [lastTokenCheck, setLastTokenCheck] = useState<number>(0);
  
  // k yap - logout fonksiyonunu nce tanmlayalm
  const logout = useCallback(() => {
    console.log('Oturum kapatlyor...');
    localStorage.removeItem('user');
    localStorage.removeItem('token');
    setUser(null);
    setToken(null);
  }, []);

  // API balantsn kontrol etme - 5 dakikada bir kontrol et
  useEffect(() => {
    const checkApiConnection = async () => {
      if (isCheckingConnection) return;
      
      try {
        setIsCheckingConnection(true);
        const isAvailable = await isServerAvailable();
        
        if (!isAvailable) {
          console.warn('API sunucusuna eriilemiyor');
        }
        
        // Sadece eer deiiklik varsa state'i gncelle
        setIsServerReachable(prev => {
          if (prev !== isAvailable) {
            console.log('API balant durumu deiti:', isAvailable ? 'bal' : 'balant yok');
          }
          return isAvailable;
        });
      } catch (err) {
        console.error('API balant kontrol srasnda hata:', err);
      } finally {
        setIsCheckingConnection(false);
      }
    };
    
    // lk alta ve her 5 dakikada bir kontrol et (60 saniyeden deitirildi)
    checkApiConnection();
    const interval = setInterval(checkApiConnection, HEALTH_CHECK_INTERVAL);
    
    return () => clearInterval(interval);
  }, [isCheckingConnection]);

  // Token dorulama ilemi
  const checkTokenValidity = useCallback(async (): Promise<boolean> => {
    try {
      const token = localStorage.getItem('token');
      
      if (!token) {
        console.error('Token bulunamad, ynlendirme yaplyor...');
        return false;
      }
      
      // Son kontrol zaman kontrol
      const now = Date.now();
      if (lastTokenCheck && (now - lastTokenCheck < 60000)) {  // 60 saniye
        console.log('Son token kontrolnden bu yana yeterli sre gemedi, kontrol atlanyor');
        return true; // Son kontrolden sonra ksa sre getiyse, token halen geerli kabul edilir
      }
      
      console.log('Token dorulamas yaplyor...');
      
      // Token formatn kontrol et
      if (!isValidToken(token)) {
        console.error('Geersiz token format, oturum kapatlyor');
        logout();
        return false;
      }
      
      setLastTokenCheck(now);
      
      try {
        // Token kontrol yaparken sunucu balant durumunu gncellemeyelim
        // token kontrolnden bamsz olarak yaplyor
        console.log('Token dorulamas yaplyor...');
        
        // AbortController ekle ve zaman am deerini azalt
        const controller = new AbortController();
        const timeoutId = setTimeout(() => {
          console.log('Token kontrol zaman amna urad, iptal ediliyor...');
          controller.abort();
        }, 5000); // 5 saniye sonra iptal et
        
        try {
          // Headers' aka belirt
          const response = await axios.get(`${API_URL}/auth/me`, {
            headers: {
              'Authorization': `Bearer ${token}`,
              'Content-Type': 'application/json',
              'Cache-Control': 'no-cache, no-store, must-revalidate',
              'Pragma': 'no-cache',
              'Expires': '0'
            },
            timeout: 5000,
            signal: controller.signal
          });
          
          // Zamanlaycy temizle
          clearTimeout(timeoutId);
          
          console.log('Token dorulamas baarl');
          const userData = response.data.user || response.data;
          
          // Kullanc bilgilerini gncelle
          if (userData) {
            setUser(userData);
            localStorage.setItem('user', JSON.stringify(userData));
            return true;
          }
          return false;
        } catch (requestError: any) {
          // Zamanlaycy temizle
          clearTimeout(timeoutId);
          
          console.error('Token geerli deil veya sresi dolmu:', requestError.message);
          
          // Hata yantn kontrol et
          if (requestError.response) {
            console.log('Hata yant:', requestError.response.status);
            
            // 401 Unauthorized hatas - oturum geersiz, k yap
            if (requestError.response.status === 401) {
              console.warn('401 Unauthorized hatas, oturum kapatlyor');
              logout();
              return false;
            }
          } else if (requestError.name === 'AbortError' || requestError.code === 'ECONNABORTED') {
            console.warn('Token kontrol zaman amna urad veya iptal edildi');
            // Zaman amnda oturumu kapatma, sadece false dndr
            return false;
          }
          
          return false;
        }
      } catch (error: any) {
        console.error('Token dorulama genel hatas:', error);
        // Oturum sresi dolmusa otomatik k yap
        logout();
        return false;
      }
    } catch (error: any) {
      console.error('Token dorulama genel hatas:', error);
      // Oturum sresi dolmusa otomatik k yap
      logout();
      return false;
    }
  }, [user, lastTokenCheck, logout]);

  // Sayfa yklendiinde localStorage'dan kullanc bilgilerini al
  useEffect(() => {
    const loadUser = async () => {
      try {
        setLoading(true);
        
        const storedUser = localStorage.getItem('user');
        const storedToken = localStorage.getItem('token');
        
        if (storedUser && storedToken) {
          // Token formatn kontrol et
          if (!isValidToken(storedToken)) {
            console.error('Geersiz token format');
            logout();
            return;
          }
          
          try {
            const userData = JSON.parse(storedUser);
            setUser(userData);
            setToken(storedToken);
            
            // Token geerliliini kontrol et - giri sonras ynlendirme hz iin burada kontrol etmeyebiliriz
            // ve dorudan ilk verileri gsterip arka planda dorulama yapabiliriz
            setLastTokenCheck(Date.now());
          } catch (parseErr) {
            console.error('Kullanc verisi JSON formatnda deil:', parseErr);
            logout();
          }
        }
      } catch (err) {
        console.error('Kullanc bilgileri yklenirken hata olutu:', err);
        // Herhangi bir hata durumunda k yap
        logout();
      } finally {
        setLoading(false);
        setInitialized(true);
      }
    };
    
    loadUser();
  }, []);

  // Token formatn kontrol eden yardmc fonksiyon
  const isValidToken = (token: string): boolean => {
    if (!token) return false;
    
    // Token formatn kontrol et (rnek: JWT kontrol)
    // JWT format: xxxxx.yyyyy.zzzzz
    const parts = token.split('.');
    return parts.length === 3;
  };

  // Hata mesajn temizle
  const clearError = () => setError(null);

  // Giri yap
  const login = async (username: string, password: string): Promise<boolean> => {
    clearError();
    setLoading(true);
    
    try {
      // Gerek API istei
      const response = await api.post('/auth/login', {
        username,
        password
      });
      
      const userData = response.data;
      const userToStore = userData.user || userData; // API yantna bal olarak ayarla
      const tokenToStore = userData.token || userData.accessToken;
      
      if (!tokenToStore) {
        throw new Error('Token alnamad');
      }
      
      // Token formatn kontrol et
      if (!isValidToken(tokenToStore)) {
        console.error('API yantnda geersiz token format');
        setError('Oturum ama srasnda bir sorun olutu');
        return false;
      }
      
      localStorage.setItem('user', JSON.stringify(userToStore));
      localStorage.setItem('token', tokenToStore);
      
      setUser(userToStore);
      setToken(tokenToStore);
      setLastTokenCheck(Date.now());
      return true;
    } catch (err: any) {
      console.error('Login error:', err);
      
      if (err.code === 'ECONNABORTED' || err.message === 'Network Error') {
        setError('Sunucuya balanlamyor. Ltfen internet balantnz kontrol edin.');
      } else {
        setError(err.response?.data?.error || 'Giri yaplrken bir hata olutu');
      }
      
      return false;
    } finally {
      setLoading(false);
    }
  };

  // Kayt ol
  const register = async (userData: RegisterData): Promise<boolean> => {
    clearError();
    setLoading(true);
    
    try {
      // Gerek API istei
      const response = await api.post('/auth/register', userData);
      
      const responseData = response.data;
      const userToStore = responseData.user || responseData;
      const tokenToStore = responseData.token || responseData.accessToken;
      
      if (!tokenToStore) {
        throw new Error('Token alnamad');
      }
      
      localStorage.setItem('user', JSON.stringify(userToStore));
      localStorage.setItem('token', tokenToStore);
      
      setUser(userToStore);
      setToken(tokenToStore);
      setLastTokenCheck(Date.now());
      return true;
    } catch (err: any) {
      console.error('Register error:', err);
      
      if (err.code === 'ECONNABORTED' || err.message === 'Network Error') {
        setError('Sunucuya balanlamyor. Ltfen internet balantnz kontrol edin.');
      } else {
        setError(err.response?.data?.error || 'Kayt olurken bir hata olutu');
      }
      
      return false;
    } finally {
      setLoading(false);
    }
  };

  // Uygulama hala ilk yklemesini tamamlamadysa, ykleniyor gster
  if (!initialized) {
    return (
      <Box sx={{ 
        display: 'flex', 
        justifyContent: 'center', 
        alignItems: 'center', 
        height: '100vh' 
      }}>
        <CircularProgress size={40} />
      </Box>
    );
  }

  return (
    <AuthContext.Provider 
      value={{ 
        user, 
        token, 
        loading,
        error,
        login,
        register,
        logout,
        setUser,
        clearError,
        checkTokenValidity,
        isServerReachable
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};

// Context kullanm iin hook
export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
}; 